.btn {
    position: absolute;
    right: 0;
    top: 0;
    width: 120px;
    height: 50px;
    background: teal;
    text-align: center;
    line-height: 50px;
    color: oldlace;
    cursor: pointer;
}

nav {
    position: absolute;
    right: 0;
    top: 50px;
    width: 120px;
    height: 120px;
    line-height: 40px;
    text-align: center;
    background-color: rgba(255, 255, 255, 0.5);
    transition: all 0.5s;
}

.menu_01:hover , .menu_02:hover, .menu_03:hover {
    background: #474747;
    color: #fff;
}

nav.act {
    position: absolute;
    right: -120px;
    transition: all 0.5s;
}